/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
a,
button {
  appearance: none;
  outline: none;
}

button,
input[type='button'],
input[type='submit'] {
  font-family: "Montserrat", Tahoma, Verdana, Segoe, sans-serif;
  background: none;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 0;
}

.btn {
  background-color: #278ed0;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  line-height: 20px;
  padding: 14px 30px;
  min-width: 206px;
  border-radius: 24px;
  text-align: center;
  transition: 0.3s ease-in-out;
  display: inline-block;
}

@media only screen and (min-width: 1025px) {
  .no-touch .btn:hover {
    box-shadow: 0 10px 16px rgba(39, 142, 208, 0.16);
  }
}

.btn--small {
  min-width: 156px;
}

.btn--wide {
  min-width: 270px;
}

.btn--mint {
  background-color: #28d2c6;
}

input,
textarea {
  background: #fff;
  border-radius: 0;
  box-shadow: none;
  color: #000;
  height: 25px;
  padding: 0 10px;
}

input::placeholder,
textarea::placeholder {
  color: #000;
}

/*--------------------------------------------------------------
# Plugins
--------------------------------------------------------------*/
.slick-slider {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  display: block;
  position: relative;
  touch-action: pan-y;
  user-select: none;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-list {
  display: block;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: relative;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-track {
  display: block;
  left: 0;
  position: relative;
  top: 0;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
  outline: 0;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

